.movie-card {
  width: 19%;
}
.movie-img {
  width: 100%;
  height: auto;
  border-radius: 8px;
  overflow: hidden;
  display: flex;
  flex-direction: column;
}
.movie-img img {
  width: 100%;
  height: 100%;
}
.movie-rating{
    width: 100%;
    background-color: #000000;
    color: #fff;
    padding: 8px 15px;
    display: flex;
    gap: 15px;
    font-size: 18px;
}
